body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-size: 16px;
}
.index-wrapper {
  width: 100vw;
  max-width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1, h2, h3, h4, h5 {
  margin: 0;
}
a {
  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}
.app-wrapper {
    width: 100%;
    max-width: 600px;
    height: 600px;
    border-radius: 10px;
    border: 1px rgb(200, 200, 200) solid;
}
.app-safe-area {
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    padding: 20px;
}
.quarter-height-wrapper {
    height: 25%;
    width: 100%;
}
.grey {
    color: rgb(120, 120, 120)
}
.center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.attributions-wrapper {
    margin-top: 5px;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
}
.attributions-wrapper a:hover {
    color: rgb(29, 161, 242);
}
.header-wrapper {
    text-transform: capitalize;
    width: calc(100% - 20px);
    margin: 0 10px;
}
.header-wrapper h1{
    font-weight: 500;
    font-size: 24px;
    margin: 10px 0;
}
.header-wrapper h2{
    font-weight: 500;
    font-size: 16px;
    margin: 2px 0;
}
.daychart-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.daily-wrapper {
    width: 60px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    border-radius: 10px;
}
.daily-wrapper:hover {
    background-color: rgba(0,0,0,0.1);
}
.daily-temp {
    font-size: 14px;
}
.daily-temp-high, .daily-temp-low {
    margin: 0 5px;
}
.daily-temp-high {

}
.daily-temp-low {
    color: rgb(120, 120, 120);
}
.hour-chart-wrapper {
    padding: 0 10px;
    width: calc(100% - 20px);
    overflow: hidden;
}
.hour-chart-wrapper svg {
    -webkit-transition: 0.5s;
    transition: 0.5s;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
.selected-summary-wrapper {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
}
.selected-summary-left-wrapper, .selected-summary-right-wrapper {
    width: 50%;
    height: 100%;
    display: flex;
}
.selected-summary-left-wrapper {
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
}
.selected-summary-right-wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-weight: 500;
}
.selected-summary-right-item {
    margin-bottom: 5px;
}
.selected-summary-temperature {
    font-size: 80px;
    line-height: 80px;
}
.unit-switcher-wrapper {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
.unit-switcher-button-inactive, .unit-switcher-button-active{

}
.unit-switcher-button-inactive {
    color: rgba(0,0,0,0.3);
    cursor: pointer;
}
.unit-switcher-button-active {
    color: black;
    cursor: auto;
}
.loading-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.loading-text-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 24px;
}
.loading-span{
    -webkit-animation-name: text-jump;
            animation-name: text-jump;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@-webkit-keyframes text-jump {
    0%   {-webkit-transform: translateY(0px);transform: translateY(0px)}
    10%   {-webkit-transform: translateY(-20px);transform: translateY(-20px)}
    20% {-webkit-transform: translateY(0);transform: translateY(0)}
}
@keyframes text-jump {
    0%   {-webkit-transform: translateY(0px);transform: translateY(0px)}
    10%   {-webkit-transform: translateY(-20px);transform: translateY(-20px)}
    20% {-webkit-transform: translateY(0);transform: translateY(0)}
}
.loading-timeout-active, .loading-timeout-inactive {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    -webkit-transition: 1s;
    transition: 1s;
    margin: 50px 0;
}
.loading-timeout-inactive {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
}
.loading-timeout-active {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.loading-timeout-message {
    font-size: 16px;
    text-transform: none;
    width: 80%;
}
.loading-refresher {
    text-transform: capitalize;
    font-size: 16px;
    cursor: pointer;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-weight: 700;
}
.loading-refresher:hover {
    color: rgb(29, 161, 242);
}
